import React, { useState } from "react";
import { connect } from "react-redux";
import { IoMdEye, IoMdEyeOff } from "react-icons/io";
import {
  FC_CreateProvince,
  FC_DeleteProvince,
  FC_UpdateProvince,
} from "../../actions/location";
import axios from "axios";
import { API } from "../../utils/api";
import {
  DistrictInterface,
  RoomLocationInterface,
} from "../../shared/interfaces";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import { isEmptyOrSpaces } from "../../shared/dataTest";

interface AppProps {
  thisProvince: RoomLocationInterface | null;
  thisDistrict: DistrictInterface | null;
  loadingRequest: boolean;
  locations: RoomLocationInterface[];
  actionTypes:
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_SECTOR"
    | "UPDATE_SECTOR"
    | null;
  setLoadingRequest: React.Dispatch<React.SetStateAction<boolean>>;
  setThisProvince: React.Dispatch<
    React.SetStateAction<RoomLocationInterface | null>
  >;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_SECTOR"
      | "UPDATE_SECTOR"
      | null
    >
  >;
  FC_CreateProvince: Function;
  FC_UpdateProvince: Function;
  FC_DeleteProvince: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const ViewProvince: React.FC<AppProps> = (props) => {
  const [provinceTitle, setProvinceTitle] = useState<string>("");
  const [provinceCode, setProvinceCode] = useState<number>(0);
  const [error, setError] = useState<{ element: string; msg: string } | null>(
    null
  );

  const handleSubmit = async () => {
    if (isEmptyOrSpaces(provinceTitle)) {
      return setError({
        element: "province_title",
        msg: "Title of Province is required",
      });
    }

    if (provinceCode <= 0) {
      return setError({
        element: "province_code",
        msg: "Code of Province is required",
      });
    }

    props.setLoadingRequest(true);
    try {
      let res;
      if (props.actionTypes === "CREATE_PROVINCE") {
        res = await axios.post<{ data: RoomLocationInterface }>(
          `${API.APPLICATION}/province`,
          {
            province_name: provinceTitle,
            province_code: provinceCode,
            districts: [],
          }
        );

        if (res.data) {
          props.FC_CreateProvince(res.data.data);
          props.PushPopupAlert({
            title: "New Province created successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes(null);
          setProvinceTitle("");
        }
      } else if (
        props.thisProvince &&
        props.actionTypes === "UPDATE_PROVINCE"
      ) {
        const districts: string[] = props.thisProvince.districts.map(
          (district) => district._id
        );
        res = await axios.patch<{ updated: RoomLocationInterface }>(
          `${API.APPLICATION}/province`,
          {
            province_id: props.thisProvince._id,
            province_name: provinceTitle,
            province_code: provinceCode,
            districts,
          }
        );
        if (res.data) {
          console.log({ res: res.data.updated });
          props.FC_UpdateProvince(res.data.updated);
          props.PushPopupAlert({
            title: "Province updated successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes(null);
          setProvinceTitle("");
          props.setThisProvince(null);
        }
      }
    } catch (error) {
      console.error({ province_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
  };

  // const handleDelete = async (provinceId: string) => {
  //   try {
  //     const res = await axios.delete<RoomLocationInterface>(
  //       `${API.APPLICATION}/province/${provinceId}`
  //     );

  //     if (res.data) {
  //       console.log({ res });
  //       props.FC_DeleteProvince(provinceId);
  //       props.PushPopupAlert({
  //         title: "Province deleted successfully",
  //         theme: PopupAlertTheme.success,
  //       });
  //       props.setLoadingRequest(false);
  //       props.setActionTypes(null);
  //     }
  //   } catch (error) {
  //     console.log({ province_error: error });
  //     props.PushPopupAlert({
  //       title: errorToText(error),
  //       theme: PopupAlertTheme.danger,
  //     });
  //     props.setLoadingRequest(false);
  //   }
  // };

  return (
    <div className={`w-full p-2`}>
      {props.locations.length > 0 ? (
        props.locations.map((province, index) => (
          <div
            key={index + province._id}
            className="flex justify-between items-center p-2 mb-3 rounded border border-gray-100 shadow"
          >
            <div className="flex items-center">
              <div className="ml-2">
                <div className="text-xl font-semibold text-gray-600">
                  {province.province_name}
                </div>
                <div className="text-sm font-light text-gray-500">
                  {province.districts.length > 0
                    ? province.districts.length + "District(s)"
                    : null}
                </div>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <button
                title={
                  props.thisProvince &&
                  props.thisProvince._id === province._id &&
                  props.actionTypes !== "UPDATE_PROVINCE"
                    ? "Close Districts"
                    : "View Districts"
                }
                className={`${
                  props.thisProvince &&
                  props.thisProvince._id === province._id &&
                  props.actionTypes !== "UPDATE_PROVINCE"
                    ? "bg-gray-500 hover:bg-gray-600"
                    : "bg-gray-400 hover:bg-gray-500"
                } p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none`}
                onClick={() => {
                  !props.thisProvince || props.thisProvince._id !== province._id
                    ? props.setThisProvince(province)
                    : props.setThisProvince(null);
                  setProvinceTitle("");
                  props.setActionTypes(null);
                }}
              >
                {props.thisProvince &&
                props.thisProvince._id === province._id &&
                props.actionTypes !== "UPDATE_PROVINCE" ? (
                  <IoMdEyeOff className="text-white w-4 h-4" />
                ) : (
                  <IoMdEye className="text-white w-4 h-4" />
                )}
              </button>
              {/* <button
                title="Update Province"
                className="bg-blue-500 hover:bg-blue-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none"
                onClick={() => {
                  props.setThisProvince(province);
                  setProvinceTitle(province.province_name);
                  setProvinceCode(province.province_code);
                  props.setActionTypes("UPDATE_PROVINCE");
                }}
                disabled={props.loadingRequest || props.actionTypes !== null}
              >
                <MdCreate className="text-white w-4 h-4" />
              </button>
              <button
                title="Delete Sub-Category"
                className="bg-red-500 hover:bg-red-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none"
                onClick={() => handleDelete(province._id)}
                disabled={props.loadingRequest || props.actionTypes !== null}
              >
                <MdClear className="text-white w-4 h-4" />
              </button> */}
            </div>
          </div>
        ))
      ) : (
        <NoResult
          theme={NoResultThemes.Dark}
          value="No province found!"
          component={
            <div className="w-full mt-2 flex flex-row items-center justify-center">
              <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                <li
                  onClick={() => props.setActionTypes("CREATE_PROVINCE")}
                  className="underline text-gray-500 cursor-pointer"
                >
                  Add province
                </li>
              </ul>
            </div>
          }
        />
      )}

      {/* modal for creating new categories or sub category */}
      {props.actionTypes &&
        (props.actionTypes === "CREATE_PROVINCE" ||
          props.actionTypes === "UPDATE_PROVINCE") && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={(): void => {
              props.setActionTypes(null);
              setProvinceTitle("");
              setProvinceCode(0);
              setError(null);
            }}
            displayClose={false}
            backDropClose={false}
            title={
              <div className="flex items-center space-x-5">
                <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                  <h2 className="uppercase text-back">
                    {props.actionTypes === "CREATE_PROVINCE"
                      ? "CREATING NEW PROVINCE"
                      : props.actionTypes === "UPDATE_PROVINCE" &&
                        "UPDATING EXISTING PROVINCE"}
                  </h2>
                </div>
              </div>
            }
            widthSizeClass={ModalSize.medium}
            padding={{ title: true, body: false, footer: false }}
          >
            <div className="mx-5 mb-4">
              <div className="py-2 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                <div className="flex flex-col">
                  <label>Province name</label>
                  <input
                    type="text"
                    className={`bg-white py-1 px-2 border-2 ${
                      error && error.element === "province_title"
                        ? "border-red-700"
                        : "border-gray-700"
                    } rounded focus:outline-none focus:bg-gray-100`}
                    onChange={(e) => {
                      error &&
                        error.element === "province_title" &&
                        setError(null);
                      setProvinceTitle(e.target.value);
                    }}
                    value={provinceTitle}
                    disabled={props.loadingRequest}
                  />
                  {error && error.element === "province_title" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>
                <div className="flex flex-col">
                  <label>Province code</label>
                  <input
                    type="number"
                    className={`bg-white py-1 px-2 border-2 ${
                      error && error.element === "province_code"
                        ? "border-red-700"
                        : "border-gray-700"
                    } rounded focus:outline-none focus:bg-gray-100`}
                    onChange={(e) => {
                      error &&
                        error.element === "province_code" &&
                        setError(null);
                      setProvinceCode(Number(e.target.value));
                    }}
                    value={provinceCode}
                    disabled={props.loadingRequest}
                  />
                  {error && error.element === "province_code" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>
              </div>
              <div className="pt-4 flex justify-between">
                <button
                  onClick={() => {
                    props.setActionTypes(null);
                    setProvinceTitle("");
                    setProvinceCode(0);
                    setError(null);
                  }}
                  className={`btn bg-gray-400 text-white hover:bg-gray-600`}
                  type="button"
                  disabled={props.loadingRequest}
                >
                  Exit
                </button>
                <button
                  className="btn bg-primary text-white hover:bg-blue-600"
                  type="button"
                  onClick={() => handleSubmit()}
                  disabled={props.loadingRequest}
                >
                  {props.loadingRequest ? "Loading..." : "Save"}
                </button>
              </div>
            </div>
          </Modal>
        )}
      {/* end modal for creating new categories or sub category */}
    </div>
  );
};

export default React.memo(
  connect(null, {
    FC_CreateProvince,
    FC_UpdateProvince,
    FC_DeleteProvince,
    PushPopupAlert,
  })(ViewProvince)
);
