export const getUserById = (users: UserInterface[], userId: string) => {
  return users.find((item) => item.user_id === userId);
};

export enum ModalState {
  View_User = "View User Info",
  Add_Access = "Add Access To User",
  Update_Access = "Update User Access",
  Close_Modal = "Close Modal",
}

// Tobe replaced with Emmy's function
export const LocationsInArray = (
  LocationData: RoomLocationInterface[]
): LocationRoomTableInterface[] | [] => {
  let locationTemp: LocationRoomTableInterface[] = [];
  for (let p in LocationData) {
    let districts = LocationData[p].districts;
    for (let d in districts) {
      let centers = districts[d].centers;
      for (let c in centers) {
        let rooms = centers[c].rooms;
        for (let r in rooms) {
          locationTemp.push({
            province_id: LocationData[p]._id,
            province_name: LocationData[p].province_name,
            district_id: districts[d]._id,
            district_name: districts[d].district_name,
            center_id: centers[c]._id,
            center_name: centers[c].center_name,
            room_id: rooms[r]._id,
            room_name: rooms[r].room_number.toString(),
            capacity: rooms[r].capacity,
            occupied: rooms[r].occupied,
          });
        }
      }
    }
  }
  return locationTemp;
};

