  blockUser = async (user_id: string, actionType: boolean) => {
    if (!user_id) {
      this.props.PushPopupAlert({
        title: "User Access is required doesn't exist",
        theme: PopupAlertTheme.danger,
      });
    }

    this.setLoadingRequest(true);

    if (
      !window.confirm(
        `Are sure you want to ${actionType ? "block" : "activate"} this user?`
      )
    ) {
      return this.setLoadingRequest(false);
    }

    try {
      const res = await axios.patch(`${API.AUTH}/users/disactive/${user_id}`, {
        is_active: !actionType,
      });
      if (res) {
        console.log("====================================");
        console.log({ blockUserToSystem: res });
        console.log("====================================");
        actionType
          ? this.props.blockUserToSystem(user_id, this.props.currentPage)
          : this.props.unBlockUserToSystem(user_id, this.props.currentPage);

        this.props.PushPopupAlert({
          title: `User has been ${actionType ? "blocked" : "activated"}!`,
          theme: PopupAlertTheme.info,
        });
      }
    } catch (error) {
      console.error({ blockUser_ERROR: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }

    this.setLoadingRequest(false);
    user_id = "";
  };

  restUserAccess = async (user_id: string) => {
    if (!user_id) {
      this.props.PushPopupAlert({
        title: "User Access is required doesn't exist",
        theme: PopupAlertTheme.warning,
      });
    }

    if (!window.confirm(`Are sure you want to reset to default access?`)) {
      return this.setLoadingRequest(false);
    }

    this.setLoadingRequest(true);
    this.setLoadingRequestType("RESET");

    try {
      const res = await axios.delete(`${API.AUTH}/custaccess/user/${user_id}`);
      res && this.props.resetUserAccess(user_id, this.props.currentPage);
      let resetedAccess: UserInterface | undefined = this.props.users.find(
        (user) => user.user_id === user_id
      );
      if (resetedAccess) {
        resetedAccess.default_access &&
          this.setUserAccess(resetedAccess.default_access.access);
        this.props.PushPopupAlert({
          title: `Resetting User Access complete successfully`,
          theme: PopupAlertTheme.info,
        });
        this.setCurrentAccess(null);
      }
    } catch (error) {
      console.error({ reseting_user_access: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }

    this.setLoadingRequest(false);
    this.setLoadingRequestType(null);
    user_id = "";
  };

  distinctDistrict = (
    location: RoomLocationInterface[],
    province_id?: string
  ): DistrictInterface[] => {
    let ids: string[] = [],
      result: DistrictInterface[] = [];

    if (province_id) {
      result = location.find((item) => item._id === province_id)
        ? location.find((item) => item._id === province_id)!.districts
          ? location.find((item) => item._id === province_id)!.districts
          : []
        : [];
    } else {
      for (const key in location) {
        if (Object.prototype.hasOwnProperty.call(location, key)) {
          for (const keyDistrict in location[key].districts) {
            if (
              Object.prototype.hasOwnProperty.call(
                location[key].districts,
                keyDistrict
              )
            ) {
              if (!ids.includes(location[key].districts[keyDistrict]._id)) {
                ids.push(location[key].districts[keyDistrict]._id);
                result.push(location[key].districts[keyDistrict]);
              }
            }
          }
        }
      }
    }

    return result;
  };

