import React, { useState } from "react";
import { connect } from "react-redux";
import {
  FC_CreateDistrict,
  FC_DeleteDistrict,
  FC_UpdateDistrict,
} from "../../actions/location";
import axios from "axios";
import { API } from "../../utils/api";
import {
  DistrictInterface,
  RoomLocationInterface,
} from "../../shared/interfaces";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { isEmptyOrSpaces } from "../../shared/dataTest";

interface AppProps {
  thisProvince: RoomLocationInterface;
  thisDistrict: DistrictInterface | null;
  loadingRequest: boolean;
  actionTypes:
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_SECTOR"
    | "UPDATE_SECTOR"
    | null;
  setLoadingRequest: React.Dispatch<React.SetStateAction<boolean>>;
  setThisDistrict: React.Dispatch<
    React.SetStateAction<DistrictInterface | null>
  >;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_SECTOR"
      | "UPDATE_SECTOR"
      | null
    >
  >;
  FC_CreateDistrict: Function;
  FC_UpdateDistrict: Function;
  FC_DeleteDistrict: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const ViewDistrict: React.FC<AppProps> = (props) => {
  const [districtName, setDistrictName] = useState<string>("");
  const [districtCode, setDistrictCode] = useState<number>(0);
  const [error, setError] = useState<{ element: string; msg: string } | null>(
    null
  );

  const handleSubmit = async () => {
    if (isEmptyOrSpaces(districtName)) {
      return setError({
        element: "district_name",
        msg: "District name is required",
      });
    }

    if (districtCode <= 0) {
      return setError({
        element: "district_code",
        msg: "Invalid district code, code must be greater than 0",
      });
    }

    props.setLoadingRequest(true);
    try {
      let res;
      if (props.actionTypes === "CREATE_DISTRICT") {
        res = await axios.post<{ data: DistrictInterface }>(
          `${API.APPLICATION}/district`,
          {
            province_id: props.thisProvince._id,
            province_code: props.thisProvince.province_code,
            district_name: districtName,
            district_code: districtCode,
            centers: [],
          }
        );
        if (res.data) {
          console.log({
            province_id: props.thisProvince._id,
            data: res.data.data,
            res,
          });
          props.FC_CreateDistrict({
            province_id: props.thisProvince._id,
            data: {
              centers: res.data.data.centers,
              sectors: res.data.data.sectors,
              province_code: res.data.data.province_code,
              province_id: res.data.data.province_id,
              district_code: res.data.data.district_code,
              district_name: res.data.data.district_name,
              district_id: res.data.data._id,
            },
          });
          props.PushPopupAlert({
            title: "New district created successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes(null);
          setDistrictName("");
          setDistrictCode(0);
        }
      } else if (
        props.thisDistrict &&
        props.actionTypes === "UPDATE_DISTRICT"
      ) {
        const centers: string[] = props.thisDistrict.centers.map(
          (center) => center._id
        );
        res = await axios.patch<{ data: DistrictInterface }>(
          `${API.APPLICATION}/district`,
          {
            district_id: props.thisDistrict._id,
            province_code: props.thisProvince.province_code,
            district_name: districtName,
            district_code: districtCode,
            centers,
          }
        );
        if (res.data) {
          props.FC_UpdateDistrict({
            province_id: props.thisProvince._id,
            district_id: props.thisDistrict._id,
            district_name: districtName,
          });
          props.PushPopupAlert({
            title: "District updated successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes(null);
          setDistrictName("");
          props.setThisDistrict(null);
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
  };

  // const handleDelete = async (districtId: string) => {
  //   try {
  //     const res = await axios.delete<DistrictInterface>(
  //       `${API.APPLICATION}/district/${districtId}`
  //     );

  //     if (res.data) {
  //       props.FC_DeleteDistrict({
  //         province_id: props.thisProvince._id,
  //         district_id: districtId,
  //       });
  //       props.PushPopupAlert({
  //         title: "District deleted successfully",
  //         theme: PopupAlertTheme.success,
  //       });
  //       props.setLoadingRequest(false);
  //       props.setActionTypes(null);
  //     }
  //   } catch (error) {
  //     console.log({ district_error: error });
  //     props.PushPopupAlert({
  //       title: errorToText(error),
  //       theme: PopupAlertTheme.danger,
  //     });
  //     props.setLoadingRequest(false);
  //   }
  // };

  return (
    <div className={`w-full p-2`}>
      {props.thisProvince.districts.length > 0 ? (
        <div className="rounded border border-gray-100 shadow-sm animate__animated animate__fadeIn">
          <table className="w-full z-0">
            <thead className="bg-gray-50">
              <tr className="text-left">
                <th className="px-3 py-3">#</th>
                <th>District name</th>
                <th>District code</th>
                <th>Sectors</th>
                {/* <th>Status</th> */}
              </tr>
            </thead>
            <tbody className="text-base">
              {props.thisProvince.districts.map((district, index) => (
                <tr
                  key={index + district._id}
                  className={`text-left border-b border-gray-50`}
                >
                  <td className="px-3 py-1">{index + 1}</td>
                  <td>{district.district_name}</td>
                  <td>{district.district_code}</td>
                  <td>
                    {district.sectors && district.sectors.length > 0
                      ? district.sectors.length + " Sector(s)"
                      : ""}
                  </td>
                  {/* <td>
                    <div className="flex items-center gap-2">
                      <button
                        title={
                          props.thisDistrict &&
                          props.thisDistrict._id === district._id
                            ? "Close Centers"
                            : "View Centers"
                        }
                        className={`${
                          props.thisDistrict &&
                          props.thisDistrict._id === district._id
                            ? "bg-gray-500 hover:bg-gray-600"
                            : "bg-gray-400 hover:bg-gray-500"
                        } p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none`}
                        onClick={() =>
                          !props.thisDistrict ||
                          props.thisDistrict._id !== district._id
                            ? props.setThisDistrict(district)
                            : props.setThisDistrict(null)
                        }
                      >
                        {props.thisDistrict &&
                        props.thisDistrict._id === district._id ? (
                          <IoMdEyeOff className="text-white w-4 h-4" />
                        ) : (
                          <IoMdEye className="text-white w-4 h-4" />
                        )}
                      </button>
                      <button
                        title="Update district"
                        className="bg-blue-500 hover:bg-blue-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none"
                        onClick={() => {
                          props.setThisDistrict(district);
                          setDistrictName(district.district_name);
                          setDistrictCode(district.district_code);
                          props.setActionTypes("UPDATE_DISTRICT");
                        }}
                        disabled={
                          props.loadingRequest || props.actionTypes !== null
                        }
                      >
                        <MdCreate className="text-white w-4 h-4" />
                      </button>
                      <button
                        title="Delete Sub-Category"
                        className="bg-red-500 hover:bg-red-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none"
                        onClick={() => handleDelete(district._id)}
                        disabled={props.loadingRequest}
                      >
                        <MdClear className="text-white w-4 h-4" />
                      </button>
                    </div>
                  </td> */}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <NoResult
          theme={NoResultThemes.Dark}
          value={"No district found!"}
          component={
            <div className="w-full mt-2 flex flex-row items-center justify-center">
              <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                <li
                  onClick={() => props.setActionTypes("CREATE_DISTRICT")}
                  className="underline text-gray-500 cursor-pointer"
                >
                  Add district
                </li>
              </ul>
            </div>
          }
        />
      )}

      {/* modal for creating new categories or sub category */}
      {props.actionTypes &&
        (props.actionTypes === "CREATE_DISTRICT" ||
          props.actionTypes === "UPDATE_DISTRICT") && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={(): void => {
              props.setActionTypes(null);
              setDistrictName("");
              setDistrictCode(10);
              setError(null);
            }}
            displayClose={false}
            backDropClose={false}
            title={
              <div className="flex items-center space-x-5">
                <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                  {/* <p className="text-base text-gray-500 font-medium">
                    You are{" "}
                    {actionType === "CREATE_SUB" || "CREATE_CAT"
                      ? "creating"
                      : "updating"}
                  </p> */}
                  <h2 className="uppercase text-back">
                    {props.actionTypes === "CREATE_DISTRICT"
                      ? "CREATING NEW DISTRICT"
                      : props.actionTypes === "UPDATE_DISTRICT" &&
                        "UPDATING EXISTING DISTRICT"}
                  </h2>
                </div>
              </div>
            }
            widthSizeClass={ModalSize.medium}
            padding={{ title: true, body: false, footer: false }}
          >
            <div className="mx-5 mb-4">
              <div className="py-2 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                <div className="flex flex-col">
                  <label>District name</label>
                  <input
                    type="text"
                    className={`bg-white py-1 px-2 border-2 ${
                      error && error.element === "district_name"
                        ? "border-red-700"
                        : "border-gray-700"
                    } rounded focus:outline-none focus:bg-gray-100`}
                    onChange={(e) => {
                      error &&
                        error.element === "district_name" &&
                        setError(null);
                      setDistrictName(e.target.value);
                    }}
                    value={districtName}
                    disabled={props.loadingRequest}
                  />
                  {error && error.element === "district_name" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>
                <div className="flex flex-col">
                  <label>District code</label>
                  <input
                    type="number"
                    className={`bg-white py-1 px-2 border-2 ${
                      error && error.element === "district_code"
                        ? "border-red-700"
                        : "border-gray-700"
                    } rounded focus:outline-none focus:bg-gray-100`}
                    onChange={(e) => {
                      error &&
                        error.element === "district_code" &&
                        setError(null);
                      setDistrictCode(Number(e.target.value));
                    }}
                    value={districtCode}
                    disabled={props.loadingRequest}
                  />
                  {error && error.element === "district_code" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>
              </div>
              <div className="pt-4 flex justify-between">
                <button
                  onClick={() => {
                    props.setActionTypes(null);
                    setDistrictName("");
                    setDistrictCode(10);
                    setError(null);
                  }}
                  className={`btn bg-gray-400 text-white hover:bg-gray-600`}
                  type="button"
                  disabled={props.loadingRequest}
                >
                  Exit
                </button>
                <button
                  className="btn bg-primary text-white hover:bg-blue-600"
                  type="button"
                  onClick={() => handleSubmit()}
                  disabled={props.loadingRequest}
                >
                  {props.loadingRequest ? "Loading..." : "Save"}
                </button>
              </div>
            </div>
          </Modal>
        )}
      {/* end modal for creating new categories or sub category */}
    </div>
  );
};

export default React.memo(connect(null, {
  FC_CreateDistrict,
  FC_UpdateDistrict,
  FC_DeleteDistrict,
  PushPopupAlert,
})(ViewDistrict));
