import React, { Component } from "react";
import { FaFilter } from "react-icons/fa";
import {
  HiChevronDoubleLeft,
  HiChevronDoubleRight,
  HiChevronLeft,
  HiChevronRight,
} from "react-icons/hi";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdSupervisorAccount } from "react-icons/md";
import { connect } from "react-redux";
import {
  schoolLevelsStoreInterface,
  fetchAccessRoles,
  loadEducationCategories,
  PushPopupAlert,
} from "../../actions";
import {
  fetchUserAccess,
  UserAccessInterface,
  userPageLimit,
} from "../../actions/users";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import {
  UserInterface,
  SystemUserRoleInterface,
} from "../../shared/interfaces";
import UserList from "./UserList";

enum TabTypes {
  All_Users = "All Users",
  Active_Users = "Active Users",
  Blocked_Users = "Blocked Users",
}

interface AppProps {
  userList: UserAccessInterface;
  accessRoles: SystemUserRoleInterface[];
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  fetchUserAccess: Function;
  fetchAccessRoles: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppStates {
  userTabs: TabTypes;
  userData: UserInterface[];
  loadingUsers: boolean;
  filterByRoleLevel: "all" | string;
  openFilterByRole: boolean;
  loading: boolean;
  manageUser: UserInterface | null;
  changeRole: boolean;
  currentPage: number;
  lastLoadedPage: number[];
}

class _UserManagement extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);
    // this.childRef = React.createRef();

    this.state = {
      userTabs: TabTypes.All_Users,
      userData: [],
      loadingUsers: true,
      filterByRoleLevel: "all",
      openFilterByRole: false,
      loading: true,
      manageUser: null,
      changeRole: false,
      currentPage: 1,
      lastLoadedPage: [1],
    };
  }

  setUserData = (value: UserInterface[]) => {
    this.setState({ userData: value });
  };

  setLoadingUsers = (value: boolean) => {
    this.setState({ loadingUsers: value });
  };

  setUserTabs = (value: TabTypes) => {
    this.setState({ userTabs: value });
  };

  setFilerByRoleLevel = (value: "all" | string) => {
    this.setState({ filterByRoleLevel: value });
  };

  setOpenFilterByRole = (value: boolean) => {
    this.setState({ openFilterByRole: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setCurrentPage = (value: number) => {
    this.setState({ currentPage: value });
  };

  setLastLoadedPage = (value: number[]) => {
    this.setState({ lastLoadedPage: value });
  };

  setManageUser = (value: UserInterface | null) => {
    this.setState({ manageUser: value });
  };

  setChangeRole = (value: boolean) => {
    this.setState({ changeRole: value });
  };

  componentDidMount = () => {
    if (!this.props.accessRoles.length) {
      this.props.fetchAccessRoles();
    }

    // LOADING EDUCATION CATEGORY =================================
    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.props.loadEducationCategories((status: boolean) => {
        this.setLoading(status);
      });
    } else {
      this.setLoading(false);
    }
    // END LOADING EDUCATION CATEGORY =================================
    if (!this.props.userList.users) {
      this.props.fetchUserAccess(
        1,
        (status: boolean, msg: string, users?: UserInterface[]) => {
          this.setLoadingUsers(status);
          if (!status) {
            if (!isEmptyOrSpaces(msg)) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }

            if (users) {
              this.setUserData(users);
              this.setLoadingUsers(false);
              console.log("====================================");
              console.log({ setLoadingUsers: status });
              console.log("====================================");
            }
          }
        }
      );
    } else {
      this.setUserData(this.props.userList.users[0].data);
      this.setLoadingUsers(false);
    }
    // if (this.props.userList.length) {
    //   console.log({ userList: this.props.userList });
    //   // REAL CODES TO BE USED ======================
    //   // setLabelCounts({
    //   //   allUser: userList.length,
    //   //   activeUser: userList.filter((user) => user.is_active).length,
    //   //   blockedUser: userList.filter((user) => !user.is_active).length,
    //   // });
    // }
  };

  reloadUsers = (page: number) => {
    if (!this.state.lastLoadedPage.includes(page)) {
      this.props.fetchUserAccess(
        page,
        (status: boolean, msg: string, users?: UserInterface[]) => {
          this.setLoadingUsers(status);
          if (!status) {
            if (!isEmptyOrSpaces(msg)) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }

            if (users) {
              let newUserList = [...this.state.userData];
              newUserList = newUserList.concat(users);
              this.setUserData(newUserList);
              this.setLastLoadedPage([...this.state.lastLoadedPage, page]);
              this.setLoadingUsers(false);
              console.log("====================================");
              console.log({ setLoadingUsers: status });
              console.log("====================================");
            }
          }
        }
      );
    }
  };

  labelCounts = (
    data: UserInterface[]
  ): {
    allUser: number;
    activeUser: number;
    blockedUser: number;
  } => {
    return {
      allUser: data.length,
      activeUser: data.filter((user) => !user.is_active).length,
      blockedUser: data.filter((user) => user.is_active).length,
    };
  };

  filterByRole = (
    allData: UserInterface[],
    value: string = "all"
  ): UserInterface[] => {
    if (value !== "all") {
      return allData.filter(
        (user) =>
          user.default_access &&
          user.default_access.system_user_role_id === value
      );
    }
    return allData;
  };

  getRoleById = (
    roleList: SystemUserRoleInterface[],
    roleId: string
  ): SystemUserRoleInterface | undefined => {
    return roleList.find((item) => item.system_user_role_id === roleId);
  };
  // countByRole = (value: string): number => {
  //   if (this.props.userList.users) {
  //     if (value === "all") {
  //       return this.props.userList.users.length;
  //     }
  //     return this.props.userList.users.filter(
  //       (user) =>
  //         user.default_access &&
  //         user.default_access.system_user_role_id === value
  //     ).length;
  //   }

  //   return 0;
  // };

  generatePages = (total_users: number): JSX.Element => {
    const totalPages = Math.ceil(total_users / userPageLimit);
    const startPage: number = 1;
    let _pages = Array.from(Array(totalPages + 1 - startPage).keys())
      .map((i) => startPage + i)
      .slice(0, 5);

    if (totalPages > 4 && this.state.currentPage > 2) {
      if (totalPages - this.state.currentPage > 1) {
        _pages = [
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
          this.state.currentPage + 1,
          this.state.currentPage + 2,
        ];
      } else if (totalPages - this.state.currentPage === 1) {
        _pages = [
          this.state.currentPage - 3,
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
          this.state.currentPage + 1,
        ];
      } else {
        _pages = [
          this.state.currentPage - 4,
          this.state.currentPage - 3,
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
        ];
      }
    }

    let jumpToFirst: boolean = false,
      jumpToLast: boolean = false,
      moveBack: boolean = false,
      moveNext: boolean = false;

    if (totalPages > 1 && this.state.currentPage !== 1) {
      moveBack = true;
    }

    if (totalPages > 1 && this.state.currentPage !== totalPages) {
      moveNext = true;
    }

    if (totalPages > 3 && this.state.currentPage > 3) {
      jumpToFirst = true;
    }

    if (
      totalPages > 3 &&
      this.state.currentPage !== totalPages &&
      totalPages - this.state.currentPage > 2
    ) {
      jumpToLast = true;
    }

    return (
      <div className="flex text-gray-700">
        {jumpToFirst && (
          <button
            className="h-12 w-12 ml-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => this.setCurrentPage(1)}
          >
            <HiChevronDoubleLeft className="w-6 h-6" />
          </button>
        )}
        {moveBack && (
          <button
            className="h-12 w-12 ml-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200"
            onClick={() => this.setCurrentPage(this.state.currentPage - 1)}
          >
            <HiChevronLeft className="w-6 h-6" />
          </button>
        )}
        <div className="flex h-12 font-medium rounded-full mx-1 bg-gray-100">
          {_pages.map((item, index) => (
            <button
              className={`w-12 md:flex justify-center items-center gap-2 rounded-full ${
                this.state.currentPage === item
                  ? "bg-gray-600 hover:bg-gray-500 text-white cursor-auto"
                  : "hover:bg-gray-200"
              }`}
              key={Math.random()}
              disabled={this.state.currentPage === item}
              onClick={() => {
                this.setCurrentPage(item);
                this.reloadUsers(item);
              }}
            >
              {item}
            </button>
          ))}
        </div>
        {moveNext && (
          <button
            className="h-12 w-12 mr-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => {
              this.setCurrentPage(this.state.currentPage + 1);
              this.reloadUsers(this.state.currentPage + 1);
            }}
          >
            <HiChevronRight className="w-6 h-6" />
          </button>
        )}
        {jumpToLast && (
          <button
            className="h-12 w-12 mr-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => {
              this.setCurrentPage(totalPages);
              this.reloadUsers(totalPages);
            }}
          >
            <HiChevronDoubleRight className="w-6 h-6" />
          </button>
        )}
      </div>
    );
  };

  dataResult = (tabType: TabTypes) => {
    if (this.props.userList.users) {
      const indexData = this.props.userList.users.findIndex(
        (item) => item.page === this.state.currentPage
      );
      if (indexData >= 0) {
        if (tabType === TabTypes.Active_Users) {
          return this.props.userList.users[indexData].data.filter(
            (user) => user.is_active
          );
        }

        if (tabType === TabTypes.Blocked_Users) {
          return this.props.userList.users[indexData].data.filter(
            (user) => !user.is_active
          );
        }

        return this.props.userList.users[indexData].data;
      }
    }
    if (tabType === TabTypes.Active_Users) {
      return this.state.userData.filter((user) => user.is_active);
    }

    if (tabType === TabTypes.Blocked_Users) {
      return this.state.userData.filter((user) => !user.is_active);
    }

    return this.state.userData;
  };

  // userTabs: TabTypes = TabTypes.All_Users;

  render() {
    if (this.state.loading === true || this.props.userList.users === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading users...
        </div>
      );
    }

    return (
      <div>
        {/* Title */}
        {!this.state.manageUser && (
          <div className="mt-3 rounded flex flex-row justify-between items-center">
            <div className="flex flex-row items-center gap-3">
              <MdSupervisorAccount className="text-6xl text-blue-600" />
              <div className="flex flex-col">
                <span className="text-blue-600 text-2xl font-bold">
                  System Users Management
                </span>
              </div>
            </div>
          </div>
        )}

        {/* --------------------------------------------------------- */}
        <div
          className={`bg-white shadow rounded border border-gray-2 p-2 w-full overflow-x-auto ${
            this.state.manageUser ? "mt-3" : ""
          }`}
        >
          {/* FILTER SECTION ================================ */}
          {!this.state.manageUser && (
            <section className="flex items-start gap-3">
              {/* FILTER BY ROLE ========================= */}
              <div>
                <div className="my-2 font-medium text-xl flex items-center gap-2">
                  <FaFilter />
                  Filter by roles
                </div>
                <div className="flex flex-col mb-3 w-full">
                  <select
                    className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none focus:bg-gray-100 animate__animated animate__fadeIn ${
                      this.state.loadingUsers && "cursor-wait"
                    }`}
                    onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                      this.setFilerByRoleLevel(e.target.value)
                    }
                    disabled={this.state.loadingUsers}
                    value={this.state.filterByRoleLevel}
                  >
                    <option value="all">All roles</option>
                    {this.props.accessRoles.length > 0 ? (
                      this.props.accessRoles.map((item, i) => (
                        <option
                          key={Math.random()}
                          value={item.system_user_role_id}
                        >
                          {item.role}
                        </option>
                      ))
                    ) : (
                      <option value="" disabled>
                        No Role
                      </option>
                    )}
                  </select>
                </div>
              </div>
              {/* END FILTER BY ROLE ========================= */}
              {/* ----------------------------------------------------- */}
              {/* FILTER BY STATUS ========================= */}
              <div>
                <div className="my-2 font-medium text-xl flex items-center gap-2">
                  <FaFilter />
                  Filter by status
                </div>
                <div className="flex items-start md:items-center justify-start md:justify-end gap-2">
                  <select
                    className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none focus:bg-gray-100 animate__animated animate__fadeIn ${
                      this.state.loadingUsers && "cursor-wait"
                    }`}
                    onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                      this.setUserTabs(e.target.value as TabTypes)
                    }
                    disabled={this.state.loadingUsers}
                  >
                    {Object.values(TabTypes).map((item, index) => (
                      <option key={Math.random()} value={item}>
                        {item}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              {/* END FILTER BY STATUS ========================= */}
            </section>
          )}
          {this.state.manageUser && (
            <div className="flex items-center gap-3 mb-2 text-gray-600">
              <button
                className="inline-block p-3 transition rounded-full bg-gray-400 ripple hover:bg-gray-600 focus:outline-none"
                onClick={() => {
                  this.setManageUser(null);
                  this.setChangeRole(false);
                }}
              >
                <IoMdArrowRoundBack className="w-6 h-6 text-gray-50" />
              </button>

              <div className="flex flex-col hidden">
                <span className="text-gray-800 font-semibold text-2xl">
                  {this.state.manageUser.user_info.fname +
                    " " +
                    this.state.manageUser.user_info.lname}
                </span>
                <span className="text-gray-600 font-normal text-sm">
                  {this.state.manageUser.email}
                </span>
              </div>
            </div>
          )}
          {/* END FILTER SECTION ================================ */}
          {this.state.userData && (
            <section>
              <UserList
                users={this.filterByRole(
                  this.dataResult(this.state.userTabs),
                  this.state.filterByRoleLevel
                )}
                roleList={this.props.accessRoles}
                userTypes={
                  this.state.userTabs === TabTypes.Active_Users
                    ? "blocked"
                    : this.state.userTabs === TabTypes.Blocked_Users
                    ? "active"
                    : "all"
                }
                loadingUser={this.state.loadingUsers}
                setLoadingUser={this.setLoadingUsers}
                manageUser={this.state.manageUser}
                setManageUser={this.setManageUser}
                changeRole={this.state.changeRole}
                setChangeRole={this.setChangeRole}
                currentPage={this.state.currentPage}
              />

              {!this.state.loadingUsers && (
                <div className="flex flex-col items-center mb-3">
                  {this.props.userList.total_users &&
                    this.generatePages(this.props.userList.total_users)}
                </div>
              )}
            </section>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  users,
  access,
  schoolLevels,
}: StoreState): {
  userList: UserAccessInterface;
  accessRoles: SystemUserRoleInterface[];
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return { userList: users, accessRoles: access.accessRoles, schoolLevels };
};

export const UserManagment = connect(mapStateToProps, {
  fetchUserAccess,
  fetchAccessRoles,
  loadEducationCategories,
  PushPopupAlert,
})(_UserManagement);
