import React, { useState } from "react";
import { connect } from "react-redux";
import { MdClear, MdCreate } from "react-icons/md";
import {
  FC_CreateSector,
  FC_DeleteSector,
  FC_UpdateSector,
} from "../../actions/location";
import axios from "axios";
import { API } from "../../utils/api";
import { DistrictInterface, SectorInterface } from "../../shared/interfaces";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { isEmptyOrSpaces } from "../../shared/dataTest";

interface AppProps {
  thisDistrict: DistrictInterface;
  thisSector: SectorInterface | null;
  loadingRequest: boolean;
  actionTypes:
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_SECTOR"
    | "UPDATE_SECTOR"
    | null;
  setLoadingRequest: React.Dispatch<React.SetStateAction<boolean>>;
  setThisSector: React.Dispatch<React.SetStateAction<SectorInterface | null>>;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_SECTOR"
      | "UPDATE_SECTOR"
      | null
    >
  >;
  FC_CreateSector: Function;
  FC_UpdateSector: Function;
  FC_DeleteSector: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const ViewSectors: React.FC<AppProps> = (props) => {
  const [sectorName, setSectorName] = useState<string>("");
  const [sectorCode, setSectorCode] = useState<number>(0);
  const [error, setError] = useState<{ element: string; msg: string } | null>(
    null
  );

  const handleSubmit = async () => {
    if (isEmptyOrSpaces(sectorName)) {
      return setError({
        element: "sector_name",
        msg: "Sector name is required",
      });
    }

    if (sectorCode <= 0) {
      return setError({
        element: "sector_code",
        msg: "Invalid sector code, must be ",
      });
    }

    props.setLoadingRequest(true);
    try {
      let res;
      if (props.actionTypes === "CREATE_SECTOR") {
        res = await axios.post<{ data: SectorInterface }>(
          `${API.APPLICATION}/sector`,
          {
            district_id: props.thisDistrict._id,
            district_code: props.thisDistrict.district_code,
            sector_name: sectorName,
            sector_code: sectorCode,
          }
        );
        if (res.data) {
          console.log({
            district_id: props.thisDistrict._id,
            data: res.data.data,
            res,
          });
          props.FC_CreateSector({
            province_id: props.thisDistrict.province_id,
            district_id: props.thisDistrict._id,
            data: {
              district_id: res.data.data.district_id,
              district_code: res.data.data.district_code,
              sector_name: res.data.data.sector_name,
              sector_code: res.data.data.sector_code,
              sector_id: res.data.data.sector_id,
            },
          });
          props.PushPopupAlert({
            title: "New sector created successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes(null);
          setSectorName("");
        }
      } else if (props.thisSector && props.actionTypes === "UPDATE_SECTOR") {
        res = await axios.patch<{ data: DistrictInterface }>(
          `${API.APPLICATION}/sector`,
          {
            district_id: props.thisSector.district_id,
            district_code: props.thisDistrict.district_code,
            sector_id: props.thisSector.sector_id,
            sector_name: sectorName,
            sector_code: sectorCode,
          }
        );
        if (res.data) {
          props.FC_UpdateSector({
            province_id: props.thisDistrict.province_id,
            district_id: props.thisSector.district_id,
            sector_id: props.thisSector.sector_id,
            sector_name: sectorName,
            sector_code: sectorCode,
          });
          props.PushPopupAlert({
            title: "Sector updated successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes(null);
          setSectorName("");
          props.setThisSector(null);
        }
      }
    } catch (error) {
      console.log({ sector_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
  };

  const handleDelete = async (sectorId: string) => {
    try {
      const res = await axios.delete<SectorInterface>(
        `${API.APPLICATION}/sector/${sectorId}`
      );

      if (res.data) {
        props.FC_DeleteSector({
          province_id: props.thisDistrict.province_id,
          district_id: props.thisDistrict._id,
          sector_id: sectorId,
        });
        props.PushPopupAlert({
          title: "Sector deleted successfully",
          theme: PopupAlertTheme.success,
        });
        props.setLoadingRequest(false);
        props.setActionTypes(null);
      }
    } catch (error) {
      console.log({ district_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
  };

  return (
    <div className={`w-full p-2`}>
      {props.thisDistrict.sectors && props.thisDistrict.sectors.length > 0 ? (
        <div className="rounded border border-gray-100 shadow-sm animate__animated animate__fadeIn">
          <table className="w-full z-0">
            <thead className="bg-gray-50">
              <tr className="text-left">
                <th className="px-3 py-3">#</th>
                <th>Sector name</th>
                <th>Sector code</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody className="text-sm">
              {props.thisDistrict.sectors.map((sector, index) => (
                <tr
                  key={index + sector.district_id}
                  className={`text-left border-b border-gray-50`}
                >
                  <td className="px-3 py-1">{index + 1}</td>
                  <td>{sector.sector_name}</td>
                  <td>{sector.sector_name}</td>
                  <td>
                    <div className="flex items-center gap-2">
                      <button
                        title="Update sector"
                        className="bg-blue-500 hover:bg-blue-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none"
                        onClick={() => {
                          props.setThisSector(sector);
                          setSectorName(sector.sector_name);
                          setSectorCode(sector.sector_code);
                          props.setActionTypes("UPDATE_SECTOR");
                        }}
                        disabled={
                          props.loadingRequest || props.actionTypes !== null
                        }
                      >
                        <MdCreate className="text-white w-4 h-4" />
                      </button>
                      <button
                        title="Delete sector"
                        className="bg-red-500 hover:bg-red-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none"
                        onClick={() => handleDelete(sector.sector_id)}
                        disabled={props.loadingRequest}
                      >
                        <MdClear className="text-white w-4 h-4" />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <NoResult
          theme={NoResultThemes.Dark}
          value={"No sector found!"}
          component={
            <div className="w-full mt-2 flex flex-row items-center justify-center">
              <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                <li
                  onClick={() => props.setActionTypes("CREATE_SECTOR")}
                  className="underline text-gray-500 cursor-pointer"
                >
                  Add sector
                </li>
              </ul>
            </div>
          }
        />
      )}

      {/* modal for creating new categories or sub category */}
      {props.actionTypes &&
        (props.actionTypes === "CREATE_SECTOR" ||
          props.actionTypes === "UPDATE_SECTOR") && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={(): void => {
              props.setActionTypes(null);
              setSectorName("");
              setSectorCode(0);
              setError(null);
            }}
            displayClose={false}
            backDropClose={false}
            title={
              <div className="flex items-center space-x-5">
                <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                  {/* <p className="text-base text-gray-500 font-medium">
                    You are{" "}
                    {actionType === "CREATE_SUB" || "CREATE_CAT"
                      ? "creating"
                      : "updating"}
                  </p> */}
                  <h2 className="uppercase text-back">
                    {props.actionTypes === "CREATE_SECTOR"
                      ? "CREATING NEW SECTOR"
                      : props.actionTypes === "UPDATE_SECTOR" &&
                        "UPDATING EXISTING SECTOR"}
                  </h2>
                </div>
              </div>
            }
            widthSizeClass={ModalSize.medium}
            padding={{ title: true, body: false, footer: false }}
          >
            <div className="w-full px-5 mb-5">
              <div className="py-3 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                <div className="flex flex-col">
                  <label>Sector name</label>
                  <input
                    type="text"
                    className={`bg-white py-1 px-2 border-2 ${
                      error && error.element === "sector_name"
                        ? "border-red-700"
                        : "border-gray-700"
                    } rounded focus:outline-none focus:bg-gray-100`}
                    onChange={(e) => {
                      error &&
                        error.element === "sector_name" &&
                        setError(null);
                      setSectorName(e.target.value);
                    }}
                    value={sectorName}
                    disabled={props.loadingRequest}
                  />
                  {error && error.element === "sector_name" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>
                <div className="flex flex-col">
                  <label>Sector code</label>
                  <input
                    type="number"
                    className={`bg-white py-1 px-2 border-2 ${
                      error && error.element === "sector_code"
                        ? "border-red-700"
                        : "border-gray-700"
                    } rounded focus:outline-none focus:bg-gray-100`}
                    onChange={(e) => {
                      error &&
                        error.element === "sector_code" &&
                        setError(null);
                      setSectorCode(Number(e.target.value));
                    }}
                    value={sectorCode}
                    disabled={props.loadingRequest}
                  />
                  {error && error.element === "sector_code" && (
                    <span className="text-red-700">{error.msg}</span>
                  )}
                </div>
              </div>
              <div className="pt-4 flex justify-between">
                <button
                  onClick={() => {
                    props.setActionTypes(null);
                    setSectorName("");
                    setSectorCode(0);
                    setError(null);
                  }}
                  className={`btn bg-gray-400 text-white hover:bg-gray-600`}
                  type="button"
                  disabled={props.loadingRequest}
                >
                  Exit
                </button>
                <button
                  className="btn bg-primary text-white hover:bg-blue-600"
                  type="button"
                  onClick={() => handleSubmit()}
                  disabled={props.loadingRequest}
                >
                  {props.loadingRequest ? "Loading..." : "Save"}
                </button>
              </div>
            </div>
          </Modal>
        )}
      {/* end modal for creating new categories or sub category */}
    </div>
  );
};

export default React.memo(
  connect(null, {
    FC_CreateSector,
    FC_UpdateSector,
    FC_DeleteSector,
    PushPopupAlert,
  })(ViewSectors)
);
