import React, { Fragment, useEffect, useState } from "react";
import { AiOutlineFilter } from "react-icons/ai";
import { BsCaretRightFill } from "react-icons/bs";
import { connect } from "react-redux";
import { getAllRoom } from "../../actions/location";
import { StoreState } from "../../reducers";
import {
  DistrictInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
  SectorInterface,
} from "../../shared/interfaces";
import { LocationsInArray } from "../UserManagement/UserList";
import ViewDistricts from "./ViewDistricts";
import ViewProvince from "./ViewProvince";
import ViewSectors from "./ViewSectors";

export const distinctDistrict = (
  rooms: RoomLocationInterface[],
  province_id?: string
): LocationRoomTableInterface[] => {
  let ids: string[] = [],
    result: LocationRoomTableInterface[] = [];

  LocationsInArray(rooms).forEach((element) => {
    if (!ids.includes(element.district_id)) {
      if (province_id && element.province_id === province_id) {
        ids.push(element.district_id);
        result.push(element);
      } else if (province_id === undefined) {
        ids.push(element.district_id);
        result.push(element);
      }
    }
  });

  return result;
};

export const distinctCenter = (
  rooms: RoomLocationInterface[],
  district_id: string
): LocationRoomTableInterface[] => {
  let ids: string[] = [],
    result: LocationRoomTableInterface[] = [];

  LocationsInArray(rooms).forEach((element) => {
    if (
      !ids.includes(element.center_id) &&
      element.district_id === district_id
    ) {
      ids.push(element.center_id);
      result.push(element);
    }
  });
  return result;
};

export const distinctRoom = (
  rooms: RoomLocationInterface[],
  center_id: string
): LocationRoomTableInterface[] => {
  let ids: string[] = [],
    result: LocationRoomTableInterface[] = [];

  LocationsInArray(rooms).forEach((element) => {
    if (!ids.includes(element.room_id) && element.center_id === center_id) {
      ids.push(element.room_id);
      result.push(element);
    }
  });
  return result;
};

interface AppProps {
  locations: RoomLocationInterface[];
  getAllRoom: Function;
}

const _Locations: React.FC<AppProps> = (props) => {
  const [actionTypes, setActionTypes] = useState<
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_SECTOR"
    | "UPDATE_SECTOR"
    | null
  >(null);
  const [thisProvince, setThisProvince] =
    useState<RoomLocationInterface | null>(null);
  const [thisDistrict, setThisDistrict] = useState<DistrictInterface | null>(
    null
  );
  const [thisSector, setThisSector] = useState<SectorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);

  useEffect(() => {
    if (!props.locations.length && loading) {
      props.getAllRoom((status: boolean, msg: string) => {
        setLoading(status);
        if (!status && msg.length > 0) {
          console.log(msg);
        }
      });
    } else if (loading) {
      setLoading(false);
    }
  }, [loading, props]);

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading locations...
      </div>
    );
  }

  const displayTitle = () => {
    if (
      (!thisSector ||
        (thisSector &&
          (actionTypes === "CREATE_SECTOR" ||
            actionTypes === "UPDATE_SECTOR"))) &&
      thisDistrict &&
      actionTypes !== "CREATE_DISTRICT" &&
      actionTypes !== "UPDATE_DISTRICT"
    ) {
      return (
        <div className="flex items-center gap-1">
          <span
            className="cursor-pointer uppercase text-xl font-semibold text-gray-800"
            onClick={() => {
              setThisProvince(null);
            }}
          >
            province
          </span>
          <span>/</span>
          <span
            className="cursor-pointer uppercase text-xl font-semibold text-gray-800"
            onClick={() => {
              setThisDistrict(null);
            }}
          >
            district
          </span>
          <span>/</span>
          <span className="text-xl text-gray-800">
            {thisDistrict.district_name}
          </span>
        </div>
      );
    } else if (
      (!thisDistrict ||
        (thisDistrict &&
          (actionTypes === "CREATE_DISTRICT" ||
            actionTypes === "UPDATE_DISTRICT"))) &&
      thisProvince
    ) {
      return (
        <div className="flex items-center gap-2">
          <span
            className="cursor-pointer uppercase text-xl font-semibold text-gray-800"
            onClick={() => {
              setThisProvince(null);
            }}
          >
            province
          </span>
          <span>/</span>
          <span className="text-xl text-gray-800">
            {thisProvince.province_name}
          </span>
        </div>
      );
    }

    return (
      <div className="flex items-center gap-1">
        <span className="uppercase text-xl font-semibold text-gray-800">
          province
        </span>
        <span>/</span>
      </div>
    );
  };

  return (
    <Fragment>
      <div className="w-full my-5">
        {/* header title */}
        <div className="flex justify-between p-2 gap-1">
          <div className="flex flex-col">
            <span className="text-blue-600 text-2xl md:text-2xl lg:text-3xl font-bold">
              Locations Management
            </span>

            <div className="flex flex-row items-center md:gap-3 gap-2 mt-1">
              <AiOutlineFilter className="text-xl" />
              <div className="flex flex-row items-center gap-1">
                {thisProvince && (
                  <span className="text-gray-700 text-base font-bold">
                    {thisProvince.province_name}
                  </span>
                )}
                {thisDistrict && (
                  <div className="flex flex-row items-center gap-1">
                    <BsCaretRightFill />
                    <span className="text-blue-600 text-base font-bold underline">
                      {thisDistrict.district_name} district
                    </span>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        {/* end header title */}

        <div className="w-full bg-white">
          <div className="border-b px-2 py-3 flex justify-between">
            {displayTitle()}

            {/* CREATE NEW PROVINCE =====================================*/}
            {/* {(!thisProvince ||
              (thisProvince &&
                (actionTypes === "CREATE_PROVINCE" ||
                  actionTypes === "UPDATE_PROVINCE"))) && (
              <button
                type="button"
                className={`btn hover:bg-blue-500 mb-1 border-2 border-blue-500 bg-white
        hover:text-white rounded-md flex items-center font-bold text-gray-700 focus:outline-none gap-1 ${
          loadingRequest && "cursor-wait"
        } ${actionTypes !== null && "cursor-not-allowed"}`}
                onClick={() => setActionTypes("CREATE_PROVINCE")}
                disabled={actionTypes !== null || loadingRequest}
              >
                <MdAdd className="inline-block mr-1 text-xl font-bold" />
                New Province
              </button>
            )} */}
            {/* CREATE NEW DISTRICT =====================================*/}
            {/* {(!thisDistrict ||
              (thisDistrict &&
                (actionTypes === "CREATE_DISTRICT" ||
                  actionTypes === "UPDATE_DISTRICT"))) &&
              thisProvince && (
                <button
                  type="button"
                  className={`btn hover:bg-blue-500 mb-1 border-2 border-blue-500 bg-white
        hover:text-white rounded-md flex items-center font-bold text-gray-700 focus:outline-none gap-1 ${
          loadingRequest && "cursor-wait"
        } ${actionTypes !== null && "cursor-not-allowed"}`}
                  onClick={() => setActionTypes("CREATE_DISTRICT")}
                  disabled={actionTypes !== null || loadingRequest}
                >
                  <MdAdd className="inline-block mr-1 text-xl font-bold" />
                  New District
                </button>
              )} */}

            {/* CREATE NEW SECTOR =====================================*/}
            {/* {(!thisSector ||
              (thisSector &&
                (actionTypes === "CREATE_SECTOR" ||
                  actionTypes === "UPDATE_SECTOR"))) &&
              thisDistrict &&
              actionTypes !== "CREATE_DISTRICT" &&
              actionTypes !== "UPDATE_DISTRICT" && (
                <button
                  type="button"
                  className={`btn hover:bg-blue-500 mb-1 border-2 border-blue-500 bg-white
        hover:text-white rounded-md flex items-center font-bold text-gray-700 focus:outline-none gap-1 ${
          loadingRequest && "cursor-wait"
        } ${actionTypes !== null && "cursor-not-allowed"}`}
                  onClick={() => setActionTypes("CREATE_SECTOR")}
                  disabled={actionTypes !== null || loadingRequest}
                >
                  <MdAdd className="inline-block mr-1 text-xl font-bold" />
                  New Sector
                </button>
              )} */}
          </div>

          {(!thisProvince ||
            (thisProvince &&
              (actionTypes === "CREATE_PROVINCE" ||
                actionTypes === "UPDATE_PROVINCE"))) && (
            <ViewProvince
              thisProvince={thisProvince}
              thisDistrict={thisDistrict}
              loadingRequest={loadingRequest}
              locations={props.locations}
              actionTypes={actionTypes}
              setLoadingRequest={setLoadingRequest}
              setThisProvince={setThisProvince}
              setActionTypes={setActionTypes}
            />
          )}

          {(!thisDistrict ||
            (thisDistrict &&
              (actionTypes === "CREATE_DISTRICT" ||
                actionTypes === "UPDATE_DISTRICT"))) &&
            thisProvince && (
              <ViewDistricts
                thisDistrict={thisDistrict}
                loadingRequest={loadingRequest}
                actionTypes={actionTypes}
                setLoadingRequest={setLoadingRequest}
                setActionTypes={setActionTypes}
                thisProvince={thisProvince}
                setThisDistrict={setThisDistrict}
              />
            )}

          {(!thisSector ||
            (thisSector &&
              (actionTypes === "CREATE_SECTOR" ||
                actionTypes === "UPDATE_SECTOR"))) &&
            thisDistrict &&
            actionTypes !== "CREATE_DISTRICT" &&
            actionTypes !== "UPDATE_DISTRICT" && (
              <ViewSectors
                thisDistrict={thisDistrict}
                thisSector={thisSector}
                loadingRequest={loadingRequest}
                actionTypes={actionTypes}
                setLoadingRequest={setLoadingRequest}
                setThisSector={setThisSector}
                setActionTypes={setActionTypes}
              />
            )}
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  locations,
}: StoreState): {
  locations: RoomLocationInterface[];
} => {
  return { locations: locations.roomLocation };
};

export const Locations = React.memo(
  connect(mapStateToProps, {
    getAllRoom,
  })(_Locations)
);
